
"use strict";
import {scrollTo} from './commonFunc'

/**/

//click
export function navigate(e){
    e.preventDefault();
    if ($(e.target).attr("href")){
        var element = $("#" +$(e.target).attr("href"));
        scrollTo(element);
    }
}


